import React from 'react';
import { Link } from 'react-router-dom';
const Navbar = props => {
  return (
    <nav className='navbar bg-primary'>
      <h2>
        <i className='fa fa-github'></i> {props.title}
      </h2>
      <ul>
        <li>
          <Link to='/'>Home</Link>
        </li>
        <li>
          <Link to='/about'>About</Link>
        </li>
      </ul>
    </nav>
  );
};

export default Navbar;
